import React, { useState, useEffect } from 'react'

export default function PlaceholderPostHookFunc() {

   const [data, setData] = useState([]);

   useEffect(() => {
      fetch('https://jsonplaceholder.typicode.com/users/1/posts')
      .then(response => response.json())
      .then(data => {
         console.log(data);
         setData(data)
      })

   }, [])

   return (
      <>
         <h1>Functional principles</h1>
         {data.map(item => 
            <section key={item.id}><h2>{`${item.id}. ${item.title}`}</h2><p>{item.body}</p></section>
            )}
      </>
   )
}
