import React, { Component } from 'react'

export default class PlaceholderPostHookClass  extends Component {
   constructor(props) {
      super(props)
   
      this.state = {
          data: []
      }
   }
   
   componentDidMount() {
      fetch('https://jsonplaceholder.typicode.com/users/1/posts')
      .then(response => response.json())
      .then(data => {console.log(data); this.setState({data})})
   }
   
   render() {
      return (
         <>
            <h1>Task 1 - class principles</h1>
            {this.state.data.map(item => (
               <section key={item.id}><h2>{`${item.id}. ${item.title}`}</h2><p>{item.body}</p></section>
            )
               )}
         </>
      )
   }
}
