import React, { Component } from "react";
import Loader from "../Loader/Loader";
import Comment2ListHook from "./Comment2ListHook";
// import CommentListHook from './CommentListHook'

export default class Component2Hook extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: [],
      isLoading: false,
    };
  }

  selectHandler = (event) => {
    this.setState({ isLoading: true });

    fetch(
      `https://jsonplaceholder.typicode.com/users/${event.target.value}/posts`
    )
      .then((response) => response.json())
      .then((data) =>
        this.setState({
          data,
          isLoading: false,
        })
      );
    console.log(event.target.value);
  };

  render() {
    const { isLoading } = this.state;
    return (
      <div>
        <h1>Task 4, 5 and 6</h1>
        <div className="wrap">
          <p>Choose post ID:</p>
          <select onChange={this.selectHandler} defaultValue="0">
            <option value="0" disabled></option>
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
          </select>
        </div>
        {/* <CommentListHook data={this.state.data} /> */}
        {isLoading ? <Loader /> : <Comment2ListHook data={this.state.data} />}
      </div>
    );
  }
}
