// import React, { useState } from "react";

import React, { Component } from "react";
import Loader from "../Loader/Loader";

export class ComponentHook extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: [],
      isLoading: false,
    };
  }

  selectHandler = (event) => {
    this.setState({ isLoading: true });
    console.log(event.target.value);
    fetch(
      `https://jsonplaceholder.typicode.com/users/${event.target.value}/posts`
    )
      .then((response) => response.json())
      .then((data) => {
        this.setState({
          data,
          isLoading: false,
        });
        console.log(data);
      });
  };

  render() {
    const { data, isLoading } = this.state;
    return (
      <div>
        <h1>Task 2, 3</h1>
        <div className="wrap">
          <div>Choose post ID:</div>
          <select onChange={this.selectHandler} defaultValue="0">
            <option value="0" disabled></option>
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
          </select>
        </div>
        {isLoading ? (
          <Loader />
        ) : (
          data.map((item) => (
            <section key={item.id}>
              <h2>
                {item.id}. {item.title}
              </h2>
              <p>{item.body}</p>
            </section>
          ))
        )}
      </div>
    );
  }
}

export default ComponentHook;

