import React, { useEffect, useState } from 'react'


export default function Comment2ListHook(props) {
   const [data, setData] = useState([])

   let onlyEven = ()=>{
      let a = data;
      let evenPost = a.filter((item, index) => index % 2 === 0);
      setData(evenPost);
      console.log(evenPost);
   }

   useEffect(() => {
      console.log('effect');
      if(props.data.length > 0) setData(props.data)
   }, [props])

   return (
      <>
         <div><button onClick={onlyEven}>Only even comments</button></div>
         {data.map((item, index) =>
            <section key={item.id}>
               <p><b>{index + 1}. {item.title}</b></p>
               <p>{item.body}</p>
            </section>
            )}
      </>
   )
}
